.container2{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

}
.container2 .card{
  position: relative;
  height: 250px;
  width: 250px;
  margin: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  

}
.container2 .card .content{
  position: absolute;
  bottom: -160px;
  width: 100%;
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  backdrop-filter: blur(15px);
  box-shadow: 0 -10px 10px rgba(0,0,0,0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: bottom 0.5s;
}

.clear_icon{
  position: relative;
  top: -50px;
  right: 10px;
  font-size: 25px;
   color: #426696;
  float:right;
  transition: all 0.3s ease-in-out;
}
.clear_icon:hover{
  color: red; 
}
.container2 .card:hover .content{
  bottom: 0px;
}
.content{
  display: flex;
  flex-direction: column;
}
.container2 .card .content .contentBx h3{
  color: #fff;
  text-transform: capitalize;
  letter-spacing: 2px;
  font-weight: 18px;
  font-size: 18px;
  text-align: center;
  margin: 20px 0 15px;
  line-height: 1.1em;
  transition: 0.5s;
  opacity: 0;
  transform: translateY(-20px);
}

.container2 .card:hover .content .contentBx h3{
   opacity: 1;
  transform: translateY(0px);
}
.container2 .card .content .sci{
  position: relative;
  bottom: 10px;
  overflow: hidden;
  display: flex;
}
.container2 .card .content .sci li{
  list-style: none;
  margin: 0 10px;
  transform: translateY(40px);
  transition: 0.5s;
  opacity: 0;
  overflow: hidden;


}
.container2 .card:hover .content .sci li{
  opacity: 1;
    transform: translateY(0px);
 


}
.sci li button{
  border: none;
  background-color: inherit;
  font-size: 20px;
  cursor: pointer;
  color: #fff;
}
